//ifxTabs.scss
@use "~@infineon/design-system-tokens/dist/tokens";
@use "../../global/font.scss";

:host {
  display: flex;
}

.tabs {
  display: flex;
  font-family: tokens.$ifxFontFamilyBody;
}

.tabs.horizontal {
  flex-direction: column;
}

.tabs.vertical {
  flex-direction: row;
}

.tabs-list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
  font-weight: tokens.$ifxFontWeightSemibold;
}

.active-border {
  content: "";
  position: absolute;
  transition: left 0.3s ease-in-out, width 0.3s ease-in-out, top 0.3s ease-in-out, height 0.3s ease-in-out;
}

.tabs.horizontal .active-border {
  bottom: 0;
  left: 0;
  height: 2px;
  background-color: tokens.$ifxColorOcean500;
}

.tabs.vertical .tabs-list {
  flex-direction: column;
  border-bottom: none;
}

.tabs.vertical .tab-content {
  padding-top: 0px;
  padding-left: 32px;
}

.tab-item.active {
  color: tokens.$ifxColorOcean500;
}

.tab-item.disabled {
  padding: tokens.$ifxSpace100 tokens.$ifxSpace200;
  cursor: pointer;
  position: relative;
  color: tokens.$ifxColorEngineering300;
  pointer-events: none;
}

.tab-item:not(:disabled, .disabled) {
  padding: tokens.$ifxSpace100 tokens.$ifxSpace200;
  cursor: pointer;
  position: relative;

  &:hover {
    color: tokens.$ifxColorOcean500;
  }
}


.tabs.vertical .tab-item {
  border-right: 2px solid transparent;
  min-width: 7em;
}

.tabs.vertical .active-border {
  left: 0;
  top: 0;
  width: 2px;
  background-color: tokens.$ifxColorOcean500;
}


.tab-content {
  padding-top: 24px;
  padding-left: 0px;
  flex-grow: 1;
}

.tabs.small .tab-item {
  font-size: tokens.$ifxFontSizeS;
}